	import uniRate from "../../components/uni-rate/uni-rate.vue";  
    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default {  
		data() {
			return {
				datalist:[],
				elist:[],
				loadingText: '正在加载...',
				hasmore:true,
				curpage:1,
				replytype:0,
				replyTxt:'',
				logining: false,
				isshowreply:0
			}				
		},
		components: {uniRate},
        computed: mapState(['sellerHasLogin']),  
		onLoad() {
			this.loadData();
		},
		onShow() {
             console.log('页面显示..');
         },
         onReady(){
             console.log('页面初次显示');
         },
         onHide() {
             console.log('页面隐藏');
         },
         onUnload() {
             console.log('页面卸载');
         },
         onBackPress(){
             console.log('页面返回...');
         },
         onShareAppMessage() {
             console.log('分享!');
         },
         onReachBottom() {
             console.log('下拉加载...');
             console.log('正在加载中...');
			 this.curpage++;
			 this.loadData();
         },
         onPageScroll(){
             console.log('页面滚动...');
         },
         onPullDownRefresh() {
             console.log('上拉刷新...');
             uni.stopPullDownRefresh();
         },
        methods: {   
            async loadData() {  
				console.log('===sellerHasLogin==='+this.sellerHasLogin);
				
                if (!this.sellerHasLogin) {  
					uni.navigateTo({
						url:'login',
					})
                }else{
					if(this.replytype==1){
						this.showReply();
					}else if(this.replytype==0){
						this.showNoReply();
					}
				}
            },
			previewImg(src,urls){
				uni.previewImage({
					current:src,
					urls
				})
			},
			showNoReply(){
					if(!this.hasmore){
						this.loadingText = '暂无更多信息';
						return false;
					}
					this.$Request.post('seller_evaluate/list',{curpage:this.curpage,type:0}).then(res => {
						if (res.code == 200) {
							console.log(res);
							this.hasmore =res.hasmore;
							console.log(this.hasmore);
							let _elist = res.datas.eval_list;
							for(var i=0;i<_elist.length;i++){
								this.elist.push(_elist[i]);
							}
							if(!this.hasmore){
								this.loadingText = '暂无更多信息';
								return false;
							}
						}
						else{
							uni.showToast({
								icon: 'none',
								title:res.datas.error,
								duration:2000
							});
							uni.navigateTo({
								url:'login',
							});
						}
					});
			},
			showReply(){
					if(!this.hasmore){
						this.loadingText = '暂无更多信息';
						return false;
					}
					this.$Request.post('seller_evaluate/list',{curpage:this.curpage,type:1}).then(res => {
						if (res.code == 200) {
							console.log(res);
							this.hasmore =res.hasmore;
							console.log(this.hasmore);
							let _datalist = res.datas.eval_list;
							for(var i=0;i<_datalist.length;i++){
								this.datalist.push(_datalist[i]);
							}
							if(!this.hasmore){
								this.loadingText = '暂无更多信息';
								return false;
							}
						}
						else{
							uni.showToast({
								icon: 'none',
								title:res.datas.error,
								duration:2000
							});
							uni.navigateTo({
								url:'login',
							});
						}
					});
			},
			showTab(t){
				uni.hideLoading();
				this.replytype=t;
				this.hasmore=true;
				this.loadingText = '正在加载...';
				this.datalist=[];
				this.elist=[];
				this.loadData();
			},inputChange(e){
				this.replyTxt = e.detail.value;
			},
			formSubmit: function (e) {
				var _this =this;
				if(this.replyTxt ==''){ 
					uni.showToast({title: '请填写回复内容',icon:"none"});
					return false; 
				}
				if(this.isshowreply ==0){ 
					uni.showToast({title: '回复编号错误',icon:"none"});
					return false; 
				}
				
				 uni.showLoading({title: '正在处理中...'});
			    _this.logining =true;          
				_this.$Request.post('seller_evaluate/save',{explain:_this.replyTxt,id:_this.isshowreply}).then(res => {
					if (res.code == 200) {
						console.log(res);
						uni.showToast({
							icon: 'none',
							title:'回复成功！',
							duration:3000,
							success: function () {
								 setTimeout(function () {
									let len = _this.elist.length;
									for(let i=0;i<len;i++){
										if(_this.isshowreply==_this.elist[i].seval_id){
											_this.elist.splice(i, 1);
											break;
										}
									}
									
									_this.logining =false;
								 }, 2000);
							}
						});
						
					}
					else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000,
							success: function () {
								_this.logining =false;
							}
						});
						
					}
					uni.hideLoading();
				});
			},
			toShow(id){
				this.isshowreply=id;
				this.replyTxt ='';
			},
			delseval(id){
				let _this= this;
				uni.showModal({
					title: '提示',
					content: '确认要删除吗？',
					success: function (res) {
						if (res.confirm) {
							 uni.showLoading({title: '正在处理中...'});
							_this.$Request.post('seller_evaluate/delevl',{id:id}).then(res => {
								if (res.code == 200) {
									console.log(res);
									uni.showToast({
										icon: 'none',
										title:'删除成功！',
										duration:3000,
										success: function () {
											 setTimeout(function () {
												let len = _this.datalist.length;
												for(let i=0;i<len;i++){
													if(id==_this.datalist[i].seval_id){
														_this.datalist[i].reply_del=1;
														break;
													}
												}
												
												_this.logining =false;
											 }, 2000);
										}
									});
									
								}
								else{
									uni.showToast({
										icon: 'none',
										title:res.datas.error,
										duration:2000,
										success: function () {
											_this.logining =false;
										}
									});
									
								}
								uni.hideLoading();
							});
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
				
			}
			 
        }  
    }  
